* {
  padding: 0;
  margin: 0;
  box-sizing: border-box;
}

.container {
  display: flex;
  flex-flow: column;
  justify-content: center;
}

table {
  border-collapse: collapse;
  width: 70%;
  margin: 50px auto;
  box-shadow: 5px 5px 5px 5px rgba(0, 0, 0, 0.2);
}

th,
td {
  text-align: center;
  padding: 5px;
  border: 1px solid black;
}

th {
  background-color: skyblue;
}

tr {
  background-color: lightcyan;
}

.new-task {
  display: flex;
  flex-flow: column wrap;
  justify-content: center;
  align-content: center;
  width: 70%;
  text-align: center;
  margin: 20px auto;
}

button,
input {
  width: 50%;
  margin: 10px auto;
  height: 30px;
}

select {
  width: 50%;
  margin: 10px auto;
  height: 20px;
}

.red-text {
  color: red;
}
